.BodyWrapper {
    display: grid;
    grid-template-columns: 1fr;
    -webkit-align-content: center;
            align-content: center;
    justify-items: center;
}
.CharacterWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
nav {
    position: fixed;
    top: 0;
    height: 75px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    -webkit-align-content: center;
            align-content: center;
    background: lightblue;
}
.jumbotron {
    margin-top: 75px;
    height: 400px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    -webkit-align-content: center;
            align-content: center;
    background: lightgray;
}

